import React, { Component } from "react";
import img2 from "./img2.jpg";
import "./About.css";

class About extends Component {
  render() {
    return (
      <section className="about" id="about">
        <h2 className="about-header">About me...</h2>
        <img src={img2} alt="s" className="about-image" />
        <div className="about-text">
          <h3 className="about-heading"> Short Introduction</h3>
          <p className="about-paragraph">
            Lorem ipsum dolor sit amet consectetur adipisicing elit. Et autem
            blanditiis inventore sed nostrum consectetur. Illum asperiores
            veniam eligendi pariatur tenetur illo tempore dignissimos aliquid
          </p>
        </div>
      </section>
    );
  }
}

export default About;
