import React, { Component } from "react";
import PropTypes from "prop-types";
import "./Card.css";

export default class Card extends Component {
  render() {
    const { title, skills } = this.props.card;
    return (
      <div className="card-container">
        <div className="card-header">
          <img src="https://source.unsplash.com/random" alt="s" />
        </div>
        <div className="card-body">
          <p className="card-body-paragraph">{title}</p>
          <ul className="card-body-list">
            {skills.map(skill => (
              <li className="card-body-item">
                {" "}
                <i className="far fa-check-circle" /> {skill}
              </li>
            ))}
          </ul>
        </div>
        <div className="card-footer">
          <a href="/" className="card-footer-link">
            <i className="fab fa-github" /> Github
          </a>
          <a href="/" className="card-footer-link">
            <i className="fas fa-globe-europe" /> Go Online
          </a>
        </div>
      </div>
    );
  }
}

Card.propTypes = {
  card: PropTypes.object.isRequired
};
